import React from 'react'
import { Route, Switch } from 'react-router-dom'
import AdminHeader from './components/AdminHeader'
import AdminDashboardScreen from './screens/dashboard/AdminDashboardScreen'
import AdminPedidosScreen from './screens/pedidos/AdminPedidosScreen'
import AdminVerPedidosScreen from './screens/verpedido/AdminVerPedidosScreen'

const AdminRouter = () => {
    return (
        <>
        <AdminHeader/>
        <Switch>
        <Route path="/admin/dashboard" component={AdminDashboardScreen} />
        <Route path="/admin/pedidos/:pedido_id" component={AdminVerPedidosScreen} />
        <Route path="/admin/pedidos" component={AdminPedidosScreen} />


        </Switch>
        </>
    )
}

export default AdminRouter
