import React, { useContext, useEffect, useState } from 'react'
import PosContext from '../../../../../context/pos/posContext';
import { getPlatosByCategorias } from '../../../../../services/categoriaService';
import PosPlato from './PosPlato';

const PosPlatos = () => {

  const [platos,setPlatos]=useState([]);
  const [cargando,setCargando]=useState(true);
  const {categoria_global}=useContext(PosContext);

  //si no tenemos una categoria global, no mostrar nada --ok
  //si te nemos una categoria  global, cargar los platos de dicha categoria
  // si la categoria global cambia, el componente posplatos, debe actualizar la info 
  //con los nuevos platos

  useEffect(()=>{
    setCargando(true)
    if(categoria_global){
      getPlatosByCategorias(categoria_global.categoria_id).then(data=>{
      if(data.ok){
        setPlatos(data.content.Platos);
        setCargando(false);
      }
    })
  } 
  },[categoria_global]);
  
    return (
        <div className="carta__platos">
          {
             categoria_global ? 
             cargando ?
             <div className="text-light">cargando </div>
             :
             (platos.map(objPlato=>{
              return <PosPlato objPlato={objPlato} key={objPlato.plato_id} />
             
             })
             ):
             <h4 className="text-light">
               Ninguna categoria ha sido seleccionada
               </h4>
          }
            

        </div>


      
    )
}

export default PosPlatos
