import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import AuthContext from '../../../../../context/auth/authContext'
import logo from "./../../../../../assets/img/logo.svg"
import search from "./../../../../../assets/img/search.svg";



const PosHeader = () => {



const {usu_nom,cerrarSesion}=useContext(AuthContext)

    return (
      
      
        <header className="header">
    <div className="header__logo">
      <img src={logo} alt="" />
    </div>
    <div className="header__buscador">
      <img src={search} alt="" />
      <input type="text" className="header__input" placeholder="Busca un término" />
    </div>
    <div className="header__usuario m-04">
      <Link className="btn btn-success mr-4" to="/"> ir a admin
      </Link>
      <button className="btn btn-danger mr-2" onClick={()=>{
        cerrarSesion();
      }}><i class="fas fa-door-open"></i></button>
      <img src="https://randomuser.me/api/portraits/men/90.jpg"   alt="" />
      <span>{usu_nom}</span>
    </div>
  </header>
  
    )
}

export default PosHeader
